import React, { Fragment, ReactNode } from 'react';
import { ComposableMap, Geographies } from 'react-simple-maps';
import BaseComponent from '../../../base/base-component';
import { IMapChartProperties } from './map-chart-interface';
import { stateData } from './map-state-data';
import './map.scss';
import MapChartTooltip from './tooltip/map-chart-tooltip';

export default class MapChart extends BaseComponent<IMapChartProperties> {
  public render(): ReactNode {
    const { isTooltip, children } = this.props;

    return (
      <Fragment>
        <ComposableMap data-tip="" projection="geoAlbersUsa">
          <Geographies geography={stateData}>{children}</Geographies>
        </ComposableMap>
        {isTooltip ? (
          <MapChartTooltip>{this.props.tooltipBody()}</MapChartTooltip>
        ) : (
          <></>
        )}
      </Fragment>
    );
  }
}
